/* Reset margins, padding, and box-sizing for all elements */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Ensure landing-page takes full height of viewport */
.landing-page {
  background-color: transparent;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: center; 
  align-items: center;
  overflow: hidden;
}

/* Content section inside landing-page */
.landing-page .div {
  background-image: url(../assets/landing-page.jpg);
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden; 

  /* Responsive adjustments */
  @media (max-width: 768px) {
    background-size: contain;
  }

  @media (min-width: 1200px) {
    background-size: cover; 
  }
}
