  /* Modal Styling */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  width: 300px;
  max-width: 100%;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.2);
  position: relative;
  margin-right: 20px;
  z-index: 1001;
}

.modal h2 {
  margin-bottom: 1rem;
}

.modal input {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.5rem;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
}

.close-button {
  background-color: #f44336;
  color: white;
}

.submit-button {
  background-color: #4CAF50;
  color: white;
}

.button {
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  font-weight: bold;
  text-align: center;

}

