/* Styled select elements */
.styled-select {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: white;
}

.styled-select:focus {
  outline: none;
  border-color: #4285F4;
}

/* Password container */
.password-container {
  position: relative;
  margin-top: 10px;
}

.password-container input {
  width: 100%;
  padding-right: 40px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.toggle-password {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 18px;
}

/* Remember me checkbox */
.remember-me {
  margin-top: 10px;
}

.remember-me input {
  margin-right: 5px;
}

.remember-me label {
  font-size: 14px;
}

/* Modal buttons */
.modal-buttons {
  margin-top: 20px;
}

